










































































import { mapGetters, mapActions } from 'vuex';

export default {
mounted() {

  this.setGallery().then(() => {

    if(this.getGallery) {
      let received = Object.entries(this.getGallery);
      received.forEach(v => {
        let vv = v[1] as {}[];
        if(vv.length > 0) { v.push(true);
        }else { v.push(false);
        }
      });
      this.categoryItems = received;
      this.loading = false;
    }
  }).catch((err) => {
    console.log(err);
  });
  
  this.$checkImage(this.getTheserver + "/assets/ups/uploadimg/accessory-braids-close-up-2009395.jpg").then((imgData) => {
    console.log("imgData", imgData);

  }).catch((err) => {
    console.log(err, this.getTheserver);
  });

  const observer = new IntersectionObserver((entries, self) => {
    entries.forEach((entry) => {
      let target = entry.target as HTMLElement;
      if(entry.isIntersecting) {
        (function () {
          target.style.backgroundImage = `url(${ target.getAttribute('imger') })`;

        })();

        // self.unobserve(entry.target);
      }else {
          target.style.backgroundImage = ``;

      }
    });
  }, {rootMargin: "0px 0px 0px 0px",threshold: 0});

  let counter = 0;
  this.$looper(() => {
    let limit = 200;
    counter++;
    let imgs = document.querySelectorAll('[imger]') as NodeListOf<HTMLElement>;
    imgs.forEach(img => {
      observer.observe(img);
    });
    //  RUN THIS FUNCTION AGAIN IF THE CONDITION BELOW IS TRUE
    // console.log(imgs, counter);
    if(imgs.length == 0 && counter >= limit) {
      console.log("Please Reload the page.");
      this.ttInfo = "Please Reload the page. The server might be busy at the moment ):";
      this.showTooltip = true;
    }
    return imgs.length == 0 && counter < limit;
  });

},
data: () => ({
  categoryItems: <any> null,
  loading: true,
  dataSend: null,
  showTooltip: false,
  ttInfo: ''
}),
computed: {
  ...mapGetters(['getGallery', 'getTheserver'])
},
methods: {
  ...mapActions(['setGallery']),
  resizeProportion(e:Event) {
    // console.log(e);
  },
  imgClick(e:Event, item:any) {
    if(item != null) {
      if(item.link) {
        item = {
          ...item,
          curDate: new Date().getTime()
        }
        this.dataSend = item;

      }
    }else {
      console.log("Item is empty!");
    }
  },
  imgClickIden(e:Event, item:Object) {
    console.log(item);
  },
  acceptFunction() {
    console.log('hello');
    history.go();
  }
},

}
